import Image from "next/image";
import Link from "next/link";

import { floatingBanner } from "@/lib/homeData";

export function FloatingBanner() {
  return (
    <Link
      href={floatingBanner.href}
      target="_blank"
      rel="noreferrer"
      className="fixed bottom-6 right-6 z-40 hidden rounded-full shadow-lg transition hover:-translate-y-1 hover:shadow-xl sm:inline-flex"
      aria-label="Solveig template promotion"
    >
      <Image
        src={floatingBanner.image}
        alt={floatingBanner.alt}
        width={120}
        height={120}
        className="h-24 w-24"
      />
    </Link>
  );
}



