import Image from "next/image";
import Link from "next/link";

import { ButtonLink } from "@/components/ui/Button";
import {
  footerGroups,
  footerNote,
  headerLogo,
  secondaryCta,
} from "@/lib/homeData";

export function Footer() {
  return (
    <footer className="bg-zinc-950 text-zinc-200">
      <div className="mx-auto max-w-6xl px-4 py-16 sm:px-6">
        <div className="grid gap-12 md:grid-cols-[1fr,2fr]">
          <div>
            <Link href="/" aria-label="Navigate home" className="inline-flex">
              <Image
                src={headerLogo.inverse}
                alt="Namita DHAWAN logo inverse"
                width={140}
                height={28}
                className="h-7 w-auto"
              />
            </Link>
          </div>

          <div className="grid gap-12 md:grid-cols-2">
            {footerGroups.map((group) => (
              <div key={group.title} className="space-y-6">
                <h3 className="text-xs font-semibold uppercase tracking-[0.4em] text-zinc-400">
                  {group.title}
                </h3>
                <nav className="space-y-3">
                  {group.links.map((link) => {
                    const isExternal = link.isExternal ?? false;
                    const isButton = link.isButton ?? false;

                    if (isButton) {
                      return (
                        <ButtonLink
                          key={link.label}
                          href={link.href}
                          variant="secondary"
                          target={isExternal ? "_blank" : undefined}
                          rel={isExternal ? "noreferrer" : undefined}
                        >
                          {link.label}
                        </ButtonLink>
                      );
                    }

                    return (
                      <Link
                        key={link.label}
                        href={link.href}
                        target={isExternal ? "_blank" : undefined}
                        rel={isExternal ? "noreferrer" : undefined}
                        className="flex items-center text-sm font-medium uppercase tracking-[0.3em] text-zinc-300 transition hover:text-white"
                      >
                        {link.label}
                      </Link>
                    );
                  })}
                </nav>
              </div>
            ))}
          </div>
        </div>

        <div className="mt-16 flex flex-col gap-6 border-t border-white/10 pt-8 md:flex-row md:items-center md:justify-end">
          <ButtonLink
            href={secondaryCta.href}
            variant="ghost"
            className="text-xs font-semibold uppercase tracking-[0.4em] text-white hover:text-zinc-900"
          >
            {secondaryCta.label}
          </ButtonLink>
        </div>
      </div>
    </footer>
  );
}



