import Image from "next/image";

import { services, servicesIntro } from "@/lib/homeData";

export function Services() {
  return (
    <section className="bg-zinc-50">
      <div className="mx-auto max-w-6xl px-4 py-24 sm:px-6">
        <div className="grid gap-16 lg:grid-cols-[minmax(0,0.9fr)_minmax(0,1.1fr)]">
          <div className="space-y-10">
            <Image
              src={servicesIntro.badgeIcon}
              alt=""
              width={50}
              height={50}
              className="h-12 w-12"
            />
            <div className="space-y-2">
              {servicesIntro.title.map((line) => (
                <h2
                  key={line}
                  className="text-4xl font-black uppercase tracking-[0.35em] text-zinc-900 md:text-5xl"
                >
                  {line}
                </h2>
              ))}
            </div>
            <div className="flex items-center gap-4 rounded-3xl border border-zinc-900/10 bg-white p-5 shadow-sm">
              <Image
                src={servicesIntro.person.avatar}
                alt={servicesIntro.person.name}
                width={80}
                height={80}
                className="h-16 w-16 rounded-2xl object-cover"
              />
              <div>
                <p className="text-base font-semibold uppercase tracking-[0.4em] text-zinc-900">
                  {servicesIntro.person.name}
                </p>
                <p className="text-xs font-medium uppercase tracking-[0.35em] text-zinc-500">
                  {servicesIntro.person.role}
                </p>
              </div>
            </div>
          </div>

          <div className="grid gap-6 md:grid-cols-2">
            {services.map((service) => (
              <div
                key={service.title}
                className="flex h-full flex-col gap-6 rounded-3xl border border-zinc-900/10 bg-white p-8 shadow-sm transition hover:-translate-y-1 hover:shadow-lg"
              >
                <div className="flex items-center gap-4">
                  <Image
                    src={service.icon}
                    alt=""
                    width={40}
                    height={40}
                    className="h-10 w-10"
                  />
                  <h3 className="text-lg font-semibold uppercase tracking-[0.35em] text-zinc-900">
                    {service.title}
                  </h3>
                </div>
                <p className="text-sm leading-relaxed text-zinc-600">
                  {service.description}
                </p>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
}



